/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {

	.eltdf-content {
		button[type="submit"] {
			padding: 12px 32px;
		}
	}
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {

                figure {
                    margin: 0;
                }
				
				a {
					
					&.woocommerce-main-image {
						width: calc(100% - 120px);
						margin: 0 0 0 120px;
					}
				}

                .thumbnails {
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: inline-block;
                    vertical-align: top;
                    width: 100px;
                    height: 100%;
                    margin: 0;

                    a {
                        float: none;
                        width: 100%;
                        margin: 0;
                        padding: 20px 0 0;

                        &:first-child {
                            padding: 0;
                        }
                    }
                }

                figure {
                    width: 100%;
                    margin: 0;
                }

                .woocommerce-product-gallery__image {

                    &:first-child {
                        padding: 0 0 0 120px;
                    }
                }

                &.woocommerce-product-gallery--with-images {

                    .woocommerce-product-gallery__image {

                        &:not(:first-child) {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100px;
                            height: 100px;
                            padding: 0;

                            a {
                                height: 100%;

                                img {
                                    height: 100%;
                                }
                            }
                        }

                        $thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                        @for $i from 0 to length($thumbnail_numbers) {
                            $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                            &:nth-child(#{$thumbnail_number_value}#{n}) {
                                $topPos: (100 * $i + 20 * $i)#{px};

                                top: $topPos;
                            }
                        }
                    }
                }
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 44%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		
		    a {
			    @include eltdfRelativeHolderLayout();
			
			    img {
				    position: relative;
				    width: 100%;
				    display: block;
			    }
		    }
		
		    .thumbnails {
			    margin: 0 -7px;
			
			    a {
				    margin: 14px 0 0;
				    padding: 0 7px;
				    box-sizing: border-box;
			    }

                $thumb_columns_label: ('columns-2', 'columns-3', 'columns-4');
                @for $i from 0 to length($thumb_columns_label) {
                    &.#{nth($thumb_columns_label, $i+1)} {
                        $column_width: 100%/($i+2);

                        a {
                            float: left;
                            width: $column_width;
                        }
                    }
                }
		    }

            figure {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 -10px;
            }

            .woocommerce-product-gallery__image {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 10px;
                box-sizing: border-box;

                &:first-child {
                    width: 100%;
                }
            }

            &.woocommerce-product-gallery--with-images {

                .woocommerce-product-gallery__image {

                    &:not(:first-child) {
                        float: left;
                        padding-top: 20px;
                    }
                }
            }

            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);

                    .woocommerce-product-gallery__image {

                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 0 0 14px;
			line-height: 1em;
	    }

		.variations {
			tr {
				display: inline-block;
			}
			td {
				&.label {
					display: block;
					font-size: 12px;
					font-weight: 500;
					letter-spacing: .2em;
					text-transform: uppercase;
					color: #212121;
					margin-bottom: 9px;
				}
				&.value select {
					margin-left: 0;
					-webkit-border-radius:3px;
					-moz-border-radius:3px;
					border-radius:3px;
				}

			}

		}
	
	    .price {
			font-size: 35px;
		    position: relative;
		    display: block;
		    margin: 17px 0 0;
		    line-height: 1;
		    color: $default-heading-color;
		
		    * {
			    font-size: inherit;
			    line-height: inherit;
		    }
		
		    del {
			    margin: 0 8px 0 0;
		    }
		
		    ins {
			    text-decoration: none;
		    }
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 18px 0 3px;
	    }
	
	    div[itemprop="description"] {
		    @include eltdfRelativeHolderLayout();
		    margin: 47px 0 23px;

		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
			padding: 19px 0;
			border-top: 1px solid #d9d9d9;
			border-bottom: 1px solid #d9d9d9;
		    margin: 28px 0 0;

			.sku_wrapper,
			.posted_in,
			.tagged_as{
				font-size: 12px;
				text-transform: uppercase;
				letter-spacing: .2em;
				color: #212121;
				font-weight: 600;
				.sku {
					color: #737373;
					font-size: 16px;
					font-weight: 400;
				}
				a {
					font-size: 12px;
					color: #737373;
					text-transform: uppercase;
					font-weight: 400;
				}
			}
		
		    > span {
			    display: block;
			    margin: 3px 0;
				line-height: 26px;
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin:25px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
				font-size: 12px;
				letter-spacing: .2em;
				font-weight: 500;
				text-transform: uppercase;
				color: $default-heading-color;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 30px 0 0;
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;

				&.description_tab,
				&.additional_information_tab,
				&.reviews_tab {
					border: 1px solid #d9d9d9;
					border-bottom: 0;
					margin-right: -4px;
				}
				
				a {
					font-size: 12px;
					text-transform: uppercase;
					letter-spacing: .2em;
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px;
					font-weight: 500;
                    width: 100%;
                    box-sizing: border-box;
				}
			}
		}
		
		.entry-content {
			@include eltdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 30%;
				padding: 0 10px 0 0;
				text-transform: uppercase;
				letter-spacing: 0.2em;
				font-size: 14px;
				font-weight: 500;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 60px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 80px;
					
					div[itemprop="description"] {
						padding: 0;
						margin: 0 0 15px;
						border: 0;
					}
					
					p {
						margin: 5px 0 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						
						&:before {
							position: relative;
							display: inline-block;
							vertical-align: middle;
							font-family: "ElegantIcons";
							content: "\e031";
							font-size: 12px;
							line-height: 1;
							letter-spacing: 2px;
							color: $default-text-color;
							overflow: hidden;
							z-index: -1;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&.active:after {
							position: absolute;
							right: 0;
							bottom: 7px;
							display: block;
							font-family: "ElegantIcons";
							font-size: 12px;
							line-height: 1;
							letter-spacing: 2px;
							color: $default-text-color;
							content: "\e033\e033\e033\e033\e033";
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;

		> h2 {
			display: block;
			margin: 36px 0 29px;
			text-align: left;
			font-size: 21px;
			line-height: 73px;
			&:after {
				content: "";
				width: 54px;
				height: 2px;
				display:block;
				background-color: $first-main-color;
			}
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */