/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.eltdf-content .woocommerce {
    @include eltdfRelativeHolderLayout();
}

.eltdf-pl-main-holder {
    margin: 0 0 22px;

    .eltdf-woo-pl-info-below-image & {

        ul.products {

            > .product {

                .button,
                .added_to_cart {
                    margin: 0;
                }
            }
        }
    }
}

ul.products {
    list-style: none;
    margin: 0 -10px;
    clear: both;
    
    > .product {
        position: relative;
        float: left;
        display: inline-block;
        vertical-align: middle;
        padding: 0 10px;
        margin: 0 0 20px;
        box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .eltdf-pl-inner .eltdf-pl-text {
                .button,
                .added_to_cart {
                    bottom: 0;
                }
            }
        }
        
        .eltdf-product-list-title {
            display: inline-block;
            text-transform: uppercase;
            margin: 0;
        }
        
        .eltdf-pl-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .price {
            display: inline-block;
            color: $default-heading-color;
            float: right;
            font-size: 12px;
            font-weight: 600;
            line-height: 27px;
            letter-spacing: .2em;
            
            * {
                font-size: inherit;
                line-height: inherit;
            }
            
            del {
                margin: 0 8px 0 0;
            }
            
            ins {
                text-decoration: none;
            }
        }
        
        .button,
        .added_to_cart {
            width: calc(100% + 2px);
            position: absolute;
            bottom: -50px;
            left: -1px;
            @include eltdfTransition(all .2s ease-out);
            text-align: center;
            height: 50px;
            line-height: 50px;
            padding: 0;
            font-size: 13px;
            text-transform: uppercase;
            letter-spacing: .5px;
            font-weight: 700;
            color: $default-heading-color;
            background-color: rgba(#fff, 0.9);
            border: 0;
            border-radius: 0;
        }
        
        .button.added {
            display: none;
        }
        
        .eltdf-pl-inner {
            @include eltdfRelativeHolderLayout();
            overflow: hidden;
            
            .eltdf-pl-image {
                @include eltdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .eltdf-pl-text {
                line-height: 0;
                display: inline-block;
                width: 100%;
                position: absolute;
                bottom: 0;
                left: 0;
                -webkit-background-size: cover;
                background-size: cover;
                background-repeat: no-repeat;
                @include eltdfTransition(height .2s ease-out);
            }
            
            .eltdf-pl-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                text-align: center;
            }
            
            .eltdf-pl-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
                
                .eltdf-product-list-title,
                .star-rating {
                    color: #fff;
                }
                
                .price {
                    color: #fff;
                    
                    del {
                        color: #ccc;
                    }
                }
            }
            
            > a {
                @include eltdfAbsoluteHolderLayout();
            }
        }
        
        .eltdf-pl-text-wrapper {
            @include eltdfRelativeHolderLayout();
            margin: 13px 0 5px;
        }
    }
}

$columns_space_label: ('no', 'small', 'normal');
$columns_space_width: (0, 10, 15);

@for $i from 0 to length($columns_space_label) {
    &.eltdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .eltdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */